import classes from "./NavBar.module.css";

function NavBar({ lIcons, title, rIcons }) {
  return (
    <div className={classes["navLayout"]}>
      <div className={classes.lNav}>
        {lIcons?.map((lIcon) => (
          <div key={Math.random()}>
            <i className={`bi bi-${lIcon}`}></i>
          </div>
        ))}
        <div className={classes.nav_title}>{title}</div>
      </div>
      <div className={classes.rNav}>
        {rIcons?.map((rIcon) => (
          <div key={Math.random()}>
            <i className={`bi bi-${rIcon}`}></i>
          </div>
        ))}
      </div>
    </div>
  );
}

export default NavBar;
